<template>
    <div>
        <div class="Post" @click="ChooseTask">
            <div class="line" v-if="showDay"> : {{ DayOfWeek[task.day] }}</div>
            <div class="line"> : {{ task.description }}</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "task",
        data(){
            return{
                DayOfWeek: {
                    "0": "",
                    "1": "",
                    "2": "",
                    "3": "",
                    "4": "",
                    "5": "",
                    "6": ""
                }
            }
        },
        props: {
            task: {
                type: Object,
                required: true
            },
            showDay: {
                type: Boolean,
                default: true
            }
        },
        methods: {
            ChooseTask(){
                this.$emit("taskChose", this.task)
            }
        }
    }
</script>

<style scoped>
    .line{
        margin-bottom: 5px;
    }
    .Post{
        padding: 10px 15px;
        border-radius: 5px;
        border: 2px solid darksalmon;
        margin-top: 35px;
        margin-right: 25px;
        margin-left: 25px;
        margin-bottom: 15px;
        user-select: none;
    }
    .Post:hover{
        background-color: #f6cbba;
    }

</style>